.login {
  background-color: #383838;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.login__container {
  width: 300px;
  display: flex;
  flex-direction: column;
  padding: 20px;
  
  box-shadow: inset 0px 0px 10px rgb(34, 34, 34),
    -8px -8px 10px 5px rgb(34, 34, 34);
  border-right: 2px solid rgb(66, 66, 66);
  border-bottom: 1px solid rgb(63, 63, 63);
}
.login__container > h1 {
  font-weight: 500;
  margin-bottom: 20px;
}
.login__container > form > h5 {
  margin-bottom: 5px;
}
.login__container > form > input {
  height: 30px;
  margin-bottom: 10px;
  background-color: white;
  width: 98%;
}
.login__container > p {
  margin-top: 15px;
  font-size: 12px;
}
.login__nombre {
  margin-top: 20px;
  margin-bottom: 20px;
  width: 150px;
  object-fit: contain;
  font-family: var(--gugi);
  color: white;
}
.login__signInButton {
  background: #f0c14b;
  border-radius: 2px;
  width: 100%;
  height: 30px;
  border: 1px solid;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
}
.login__registerButtom {
  border-radius: 2px;
  width: 100%;
  height: 30px;
  border: 1px solid;
  margin-top: 10px;
  border-color: darkgray;
  color: #111;
}
