.nav {
  width: 100%;
  height: 70px;
  display: flex;
  background: #151414;
  position: -webkit-sticky;
  position: sticky;
  right: 0;
  top: 0;
  align-items: center;
  justify-content: space-around;
  z-index: 1;
}
.nav .link {
  position: relative;
  flex: 1 1;
  /* line-height: 70px; */
  text-align: center;
  padding: 10px 10px;
}
.link{
  display: flex;
  flex-direction: column;
  margin: 0px 10px;
}
.header__link {
  display: flex;
  justify-content: space-evenly;
}
.nav .link .link__enlace {
  position: relative;
  color: #f5f5f5;
  font-size: 20px;
  text-decoration: none;
  flex: 1 1;
  z-index: 2;
  padding: 0px;
}
.link__basket{
  padding: 10px 0px;
}
.header__carrito{
  position: relative;
}
.contador{
  position: absolute;
  bottom: 35%;
  right: 0;
}
.nav .link:before {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 0%;
  background: red;
  z-index: -1;
  transition: all 300ms ease-in-out;
}
.nav .link:after {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 0%;
  background: tomato;
  transition: all 300ms ease-in-out;
}
.nav .link:hover {
  cursor: pointer;
}
.nav .link:hover:before {
  height: 100%;
  -webkit-filter: blur(30px);
          filter: blur(30px);
}
.nav .link:hover:after {
  height: 100%;
}

.nav .link__logo:before {
  content: "";
  position: absolute;
  top: 20px;
  left: 50px;
  width: 13%;
  height: 40%;
  background: tomato;
  z-index: -1;
  box-shadow: tomato 0px 0px 10px 10px;
}
.link__logo{
  font-family: var(--gugi);
  color: white;
}
.container__logo{
  display: flex;
  flex-direction: column;
  margin: 0px 10px;
}

