.effect04 {
  --uismLinkDisplay: var(--smLinkDisplay, inline-flex);
  display: var(--uismLinkDisplay);
  color: var(--gris-verdoso);
  outline: solid 2px #252525;
  background-color: #252525;
  box-shadow: 5px 5px 10px #1b1b1b;
  /* outline: solid 2px #08f; */
  position: relative;
  transition-duration: 0.4s;
  overflow: hidden;
}

.effect04::before,
.effect04 span {
  margin: 0 auto;
  transition-timing-function: cubic-bezier(0.86, 0, 0.07, 1);
  transition-duration: 0.4s;
}

.effect04:hover {
  background-color: #000;
  outline: solid 2px #08f;
  text-shadow: -0.2rem -0.2rem 1rem #fff, 0.2rem 0.2rem 1rem #fff, 0 0 2rem #f40,
    0 0 4rem #f40, 0 0 6rem #f40, 0 0 8rem #f40, 0 0 10rem #f40;
  box-shadow: 0 0 0.5rem #fff, inset 0 0 0.5rem #fff, 0 0 2rem #08f,
    inset 0 0 2rem #08f, 0 0 4rem #08f, inset 0 0 4rem #08f;
}


.effect04:hover span {
  transform: translateY(-400%) scale(-0.1, 20);
}


.effect04::before {
  content: attr(data-sm-link-text);
  color: #08f;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  transform: translateY(500%) scale(-0.1, 20);
}


.effect04:hover::before {
  letter-spacing: 0.05em;
  transform: translateY(0) scale(1, 1);
}


