.subtotal{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 300px;
    height: 100px;
    padding: 20px;
    background-color: var(--gris-claro);
    border: 1px solid #dddddd ;
    border-radius: 3px;
}
.subtotal__gift{
    display: flex;
    align-items: center;
}
.subtotal__gift > input {
    margin-right: 5px;
}
.subtotal > button{
    background-color: var(--color-primario);
    width: 100%;
    height: 30px;
    border: 1px solid;
    border-color: #a88734 #9c7e31 #846a29;
    margin-top: 10px;
    color: #111;
}